
import { Component, Prop, Vue } from 'vue-property-decorator';
import { FadeTransition } from 'vue2-transitions';

// eslint-disable-next-line no-unused-vars
import TagWidget, { TagColors, TagNames } from '@/components/tags-widget';

/**
 * Входные параметры карточки новости
 */
interface INewsCard {
  /**
   * Ширина карточки, колонок
   */
  width?: number;
  /**
   * Высота карточки, строк
   */
  height?: number;
  /**
   * Изображение
   */
  image?: string;
  /**
   * Заливка цветом
   */
  isFillByColor?: boolean;
  /**
   * Теги
   */
  tags: TagNames[];
  /**
   * Заголовок
   */
  title?: string;
  /**
   * Описание
   */
  description?: string;
  /**
   * Ссылка
   */
  link: string;
  /**
   * Отображение кнопок
   */
  isShowNavigation?: string;
}

/**
 * Карточка новости
 */
@Component<NewsCard>({
  components: {
    TagWidget,
    FadeTransition,
  },
})
export default class NewsCard extends Vue {
  @Prop({
    type: Number,
    default: 1,
    validator(value) {
      return value >= 1;
    },
  })
  width!: INewsCard['width'];

  @Prop({
    type: Number,
    default: 1,
    validator(value) {
      return value >= 1;
    },
  })
  height!: INewsCard['height'];

  @Prop({
    type: String,
  })
  image!: INewsCard['image'];

  @Prop({
    type: Boolean,
    default: false,
  })
  isFillByColor!: INewsCard['isFillByColor'];

  @Prop({
    type: Array,
    required: true,
    default() {
      return ['travel'];
    },
  })
  tags!: INewsCard['tags'];

  @Prop({
    type: String,
  })
  title!: INewsCard['title'];

  @Prop({
    type: String,
  })
  description!: INewsCard['description'];

  @Prop({
    type: String,
    required: true,
  })
  link!: INewsCard['link'];

  @Prop({
    type: Boolean,
    default: false,
  })
  isShowNavigation!: INewsCard['isShowNavigation'];

  colors = TagColors;

  /**
   * Отображать изображение
   */
  showImage: boolean = false;

  /**
   * Адаптивная ширина в зависимости от резрешения дисплея
   */
  get adaptiveWidth(): number {
    switch ((this as any).$mq) {
      case 'laptop':
        if (Number(this.width) > 3) return 3;
        break;
      case 'tablet':
        if (Number(this.width) > 2) return 2;
        break;
      case 'mobile':
        if (Number(this.width) > 1) return 1;
        break;

      default:
        break;
    }
    return Number(this.width) <= 4 ? Number(this.width) : 4;
  }

  /**
   * Позиционирование тегов
   */
  get tagsPosition(): string {
    let position = 'relative';
    if (this.showImage && this.adaptiveWidth === 1) {
      position = 'absolute';
    }
    return position;
  }
}
